<template>
  <div class="the-order-form-market">
    <e-input-number
      value="MARKET PRICE"
      disabled
      :label="`Price for 1 ${activeContractName}`"
      measurement-unit="ETH"
      class="the-order-form-market__input the-order-form-market__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
    />

    <e-input-number
      :value="amount"
      :decimals="FUTURES_AMOUNT_DECIMALS"
      label="Amount"
      :measurement-unit="activeContractName"
      :invalid="$v.amount.$dirty && ($v.amount.$invalid || !enoughBalance)"
      class="the-order-form-market__input the-order-form-market__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onAmountInput"
    />

    <e-input-number
      :value="total"
      disabled
      :decimals="ETH_DECIMALS"
      label="Total (approx.)"
      measurement-unit="USD"
      class="the-order-form-market__input the-order-form-market__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
    />

    <div class="the-order-form-market__submit the-order-form-market__item">
      <slot name="submit"/>
    </div>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';
import { validationMixin } from 'vuelidate';
import { required, decimal } from 'vuelidate/lib/validators';
import { ZERO } from '@/util/numbers';
import { ETH_DECIMALS, BUY_TOKEN_ORDER, SELL_TOKEN_ORDER } from '@/util/constants';
import { maxDecimalPlaces, greaterThanZero } from '@/util/validators';
import getOrderTotalRoundingMode from '@/util/getOrderTotalRoundingMode';

import EInputNumber from '@/components/EInputNumber';

export default {
  name: 'the-futures-order-form-market',
  inheritAttrs: false,

  props: {
    amount: {
      type: String,
      default: '',
    },

    buyOrSell: {
      type: String,
      required: true,
      validator: v => [BUY_TOKEN_ORDER, SELL_TOKEN_ORDER].includes(v),
    },

    anyDirty: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    /**
     *      
     */
    invalid() {
      return this.$v.$invalid;
    },

    /**
     *   
     */
    orderBookTotal() {
      return this.orders.reduce((acc, order) => acc.plus(order.amount), ZERO);
    },

    /**
     * ,    
     *
     *   -         ''
     */
    total() {
      if (this.invalid) return ''; // invalid form

      const orders = this.orders;
      const amount = BigNumber(this.amount);
      let total = BigNumber(0);
      let remains = amount;
      let i = 0;
      while (!remains.isZero() && i < orders.length) {
        const curOrderAmount = orders[i].size;
        if (remains.gt(curOrderAmount)) {
          remains = remains.minus(curOrderAmount);
          total = orders[i].volumeSum;
        } else {
          const lastOrderValue = remains.times(orders[i].price).shiftedBy(ETH_DECIMALS);
          total = total.plus(lastOrderValue);
          remains = BigNumber(0);
        }
        i += 1;
      }

      if (!remains.isZero()) return ''; // not enough orders in order book
      return total.shiftedBy(-ETH_DECIMALS).dp(ETH_DECIMALS, getOrderTotalRoundingMode(this.buyOrSell)).toFixed();
    },

    /**
     *    
     */
    orders() {
      return this.buyOrSell === BUY_TOKEN_ORDER ? [...this.asks].reverse() : this.bids;
    },

    ...mapGetters({
      activeContractName: 'futuresActiveContract/contractName',
      asks: 'orderBook/getAsks',
      bids: 'orderBook/getBids',
    }),
  },

  validations: {
    amount: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces(value) {
        return maxDecimalPlaces(ETH_DECIMALS)(value);
      },
    },
  },

  watch: {
    invalid: {
      immediate: true,
      handler(invalid) {
        this.$emit('update:invalid', invalid);
      },
    },

    '$v.$anyDirty': function handler(anyDirty) {
      this.$emit('update:anyDirty', anyDirty);
    },

    anyDirty: {
      immediate: true,
      handler(anyDirty) {
        if (anyDirty) {
          this.$v.$reset();
        }
      },
    },
  },

  created() {
    this.onInput();
  },

  methods: {
    onInput({ amount = this.amount } = {}) {
      this.$emit('input', { amount });
    },

    onAmountInput(amount) {
      this.$v.amount.$touch();
      this.$emit('input', { amount });
    },
  },

  components: {
    EInputNumber,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-order-form-market {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;

  &__item {
    height: 50px;
    margin-bottom: 12px;

    @include r(1200) {
      height: 40px;
      margin-bottom: 10px;
    }

    @include h(750) {
      height: 40px;
      margin-bottom: 10px;
    }

    @include rwh(2400, 850) {
      height: 60px;
      margin-bottom: 14px;
    }
  }

  &__input {
    width: calc(50% - 5px);
  }

  &__submit {
    width: calc(50% - 5px);
  }
}
</style>



