<template>
  <div
    class="e-receipt-item"
    :class="[
      bold ? 'e-receipt-item_font-bold' : '',
      buy ? 'e-receipt-item_buy'
          : sell ? 'e-receipt-item_sell' : 'e-receipt-item_num',
    ]"
  >
    <div class="e-receipt-item__title">
      <slot name="title"></slot>
    </div>
      <div class="e-receipt-item__value">
        <span v-if="buy && !icon">+</span>
        <span v-if="sell && !icon">-</span>
        <div
          v-if="icon"
          class="e-receipt-item__icon"
        >
          <e-icon :src="sell ? iconLoss : iconProfit" />
        </div>
        <slot
          v-else
          name="value"
        />
        <div class="e-receipt-item__token-name" v-if="tokenName.length !== 0">
          {{tokenName}}
        </div>
      </div>
  </div>
</template>

<script>
import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'e-receipt-item',

  created() {
    Object.assign(this, {
      iconLoss,
      iconProfit,
    });
  },

  props: {
    buy: {
      type: Boolean,
      default: false,
    },
    sell: {
      type: Boolean,
      default: false,
    },
    bold: {
      type: Boolean,
      default: false,
    },
    tokenName: {
      type: String,
      default: '',
    },
    icon: {
      type: Boolean,
      default: false,
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-receipt-item {
  $this: &;

  display: flex;
  flex-direction: row;
  padding: 10px 0;

  &__value {
    text-align: right;
    max-width: 50%;
    width: 50%;
  }

  &__title {
    text-align: right;
    max-width: 50%;
    width: 50%;
    color: $color-grey;
    font-size: 13px;
    font-weight: 600;
    padding-right: 10px;
    text-align: left;
  }

  &__value {
    align-items: center;
    display: flex;
    padding-left: 10px;
    justify-content: flex-end;
  }

  &_font-bold {
    font-weight: 800;
    font-size: 13px;

    #{$this}__title {
      color: #46484C;
      font-weight: 800;
    }
  }

  &_buy {
    color: $color-green-bright;

    .e-big-number {
      color: $color-green-bright;

      &__leading-zeros {
        color: rgba($color-green-bright, 0.5);
      }
    }

    #{$this}__token-name {
      color: $color-green-bright;
    }
  }

  &_sell {
    color: $color-red;

    .e-big-number {
      color: $color-red;

      &__leading-zeros {
        color: rgba($color-red, 0.5);
      }
    }
  }

  &_num {
    .e-big-number {
      color: $color-dark-blue;
      font-weight: 600;

      &__leading-zeros {
        color: rgba($color-dark-blue, 0.5);
      }
    }

    #{$this}__token-name {
      color: $color-dark-blue;
    }
  }

  &__token-name {
    margin-left: 5px;
    min-width: 36px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__icon {
    height: 11px;
    position: relative;
    margin-right: 8px;
    min-width: 12px;
    width: 12px;
  }

  .e-big-number {
    letter-spacing: 0.7px;
  }
}
</style>



