const BigNumber = require('bignumber.js');

class FuturesCalc {
  /**
   * Calculate needed balance in WEI
   *
   * @param {String|Number|BigNumber} limitPrice
   * @param {String|Number|BigNumber} tradePrice
   * @param {String|Number|BigNumber} qty
   * @param {Boolean} side
   * @param {String|Number|BigNumber} fee
   * @param {String|Number|BigNumber} gasFee
   * @param {Boolean} inversed
   * @param {String|Number|BigNumber} multiplier
   * @returns {BigNumber}
   */
  static calculateNeededBalance(limitPrice, tradePrice, qty, side, fee, gasFee, inversed, multiplier) {
    const calcCollateral = FuturesCalc.calculateCollateral(limitPrice, tradePrice, qty, side, inversed, multiplier);
    const calcFee = FuturesCalc.calculateFee(qty, tradePrice, fee, inversed, multiplier);

    return BigNumber(calcCollateral).plus(calcFee)
      .times('1e10')
      .plus(gasFee);
  }

  /**
   * Calculate collateral
   *
   * function calculateCollateral (
   *      uint256 limitPrice, uint256 tradePrice, uint256 qty,
   *      bool side, bytes32 futuresContractHash
   * ) view returns (uint256)
   *
   * @param {String|Number|BigNumber} limitPrice
   * @param {String|Number|BigNumber} tradePrice
   * @param {String|Number|BigNumber} qty
   * @param {Boolean} side
   * @param {Boolean} inversed
   * @param {String|Number|BigNumber} multiplier
   * @returns {BigNumber}
   */
  static calculateCollateral(limitPrice, tradePrice, qty, side, inversed, multiplier) {
    if (side) {
      // long
      if (inversed) {
        // return safeMul(safeSub(tradePrice, limitPrice), qty) / limitPrice;
        return BigNumber(tradePrice).minus(limitPrice).times(qty).div(limitPrice);
      }

      // return safeMul(
      //  safeMul(
      //    safeSub(tradePrice, limitPrice),
      //    qty
      //  ),
      //  multiplier
      // ) / 1e8 / 1e18;
      return BigNumber(tradePrice).minus(limitPrice)
        .times(qty)
        .times(multiplier)
        .div('1e8')
        .div('1e18');
    }

    // short
    if (inversed) {
      // return safeMul(safeSub(limitPrice, tradePrice), qty)  / limitPrice;
      return BigNumber(limitPrice).minus(tradePrice).times(qty).div(limitPrice);
    }
    // return safeMul(
    //  safeMul(
    //    safeSub(limitPrice, tradePrice),
    //    qty
    //  ),
    //  multiplier
    // ) / 1e8 / 1e18;
    return BigNumber(limitPrice).minus(tradePrice)
      .times(qty)
      .times(multiplier)
      .div('1e8')
      .div('1e18');
  }

  /**
   * Calculate fee
   *
   * function calculateFee (
   *      uint256 qty, uint256 tradePrice, uint256 fee,  bytes32 futuresContractHash
   * ) returns (uint256)
   *
   * @param {String|Number|BigNumber} qty
   * @param {String|Number|BigNumber} tradePrice
   * @param {String|Number|BigNumber} fee
   * @param {Boolean} inversed
   * @param {String|Number|BigNumber} multiplier
   * @returns {BigNumber}
   */
  static calculateFee(qty, tradePrice, fee, inversed, multiplier) {
    return BigNumber(FuturesCalc.calculateTradeValue(qty, tradePrice, inversed, multiplier))
      .times(fee)
      .div('1e18')
      .div('1e10');
  }

  /**
   * Calculate trade value
   *
   * function calculateTradeValue(uint256 qty, uint256 price, bytes32 futuresContractHash) returns (uint256)
   *
   * @param {String|Number|BigNumber} qty
   * @param {String|Number|BigNumber} price
   * @param {Boolean} inversed
   * @param {String|Number|BigNumber} multiplier
   * @returns {BigNumber}
   */
  static calculateTradeValue(qty, price, inversed, multiplier) {
    if (inversed) {
      return BigNumber(qty).shiftedBy(10);
    }

    return BigNumber(qty).times(price).shiftedBy(2).times(multiplier)
      .shiftedBy(-18);
  }

  /**
   * Calculate profit
   *
   * function calculateProfit(
   *      uint256 closingPrice, uint256 entryPrice, uint256 qty, bytes32 futuresContractHash, bool side
   * ) returns (uint256)
   *
   * @param {String|Number|BigNumber} closingPrice
   * @param {String|Number|BigNumber} entryPrice
   * @param {String|Number|BigNumber} qty
   * @param {Boolean} side
   * @param {Boolean} inversed
   * @param {String|Number|BigNumber} multiplier
   * @return {BigNumber}
   */
  static calculateProfit(closingPrice, entryPrice, qty, side, inversed, multiplier) {
    if (side) {
      if (inversed) {
        // return safeMul(
        //  safeSub(entryPrice, closingPrice),
        //  qty
        // ) / closingPrice;
        return BigNumber(entryPrice).minus(closingPrice)
          .times(qty)
          .div(closingPrice);
      }

      // return safeMul(
      //  safeMul(
      //    safeSub(entryPrice, closingPrice),
      //    qty
      //  ),
      //  multiplier
      // )  / 1e8 / 1e18;
      return BigNumber(entryPrice).minus(closingPrice)
        .times(qty)
        .times(multiplier)
        .div('1e8')
        .div('1e18');
    }

    if (inversed) {
      // return safeMul(
      //  safeSub(closingPrice, entryPrice),
      //  qty
      // ) / closingPrice;
      return BigNumber(closingPrice).minus(entryPrice)
        .times(qty)
        .div(closingPrice);
    }

    // return safeMul(
    //  safeMul(
    //    safeSub(closingPrice, entryPrice),
    //    qty
    //  ),
    //  multiplier
    // ) / 1e8 / 1e18;
    return BigNumber(closingPrice).minus(entryPrice)
      .times(qty)
      .times(multiplier)
      .div('1e8')
      .div('1e18');
  }

  /**
   * Calculate loss
   *
   * function calculateLoss(
   *      uint256 closingPrice, uint256 entryPrice, uint256 qty, bytes32 futuresContractHash, bool side
   * ) returns (uint256)
   *
   * @param {String|Number|BigNumber} closingPrice
   * @param {String|Number|BigNumber} entryPrice
   * @param {String|Number|BigNumber} qty
   * @param {Boolean} side
   * @param {Boolean} inversed
   * @param {String|Number|BigNumber} multiplier
   * @return {BigNumber}
   */
  static calculateLoss(closingPrice, entryPrice, qty, side, inversed, multiplier) {
    if (side) {
      if (inversed) {
        // return safeMul(
        //  safeSub(closingPrice, entryPrice),
        //  qty
        // ) / closingPrice;
        return BigNumber(closingPrice).minus(entryPrice)
          .times(qty)
          .div(closingPrice);
      }

      // return safeMul(
      //  safeMul(
      //    safeSub(closingPrice, entryPrice),
      //    qty
      //  ),
      //  multiplier
      // ) / 1e8 / 1e18;
      return BigNumber(closingPrice).minus(entryPrice)
        .times(qty)
        .times(multiplier)
        .div('1e8')
        .div('1e18');
    }

    if (inversed) {
      // return safeMul(
      //  safeSub(entryPrice, closingPrice),
      //  qty
      // ) / closingPrice;
      return BigNumber(entryPrice).minus(closingPrice)
        .times(qty)
        .div(closingPrice);
    }

    // return safeMul(
    //  safeMul(
    //    safeSub(entryPrice, closingPrice),
    //    qty
    //  ),
    //  multiplier
    // ) / 1e8 / 1e18;
    return BigNumber(entryPrice).minus(closingPrice)
      .times(qty)
      .times(multiplier)
      .div('1e8')
      .div('1e18');
  }
}

module.exports = FuturesCalc;



