<template>
  <div
    class="e-futures-order"
    :class="[
      orderType === BUY_TOKEN_ORDER ? 'e-futures-order_buy' : 'e-futures-order_sell',
      {
        'e-futures-order_canceled': isCanceled,
        'e-futures-order_filled': isFilled,
      },
      `e-futures-order__${theme}`
    ]"
  >
    <!-- TODO: Ruslan         e-futures-order__attention -->
    <div class="e-futures-order__table-col">
      <div
        class="e-futures-order__token-order"
        :class="[ orderType === BUY_TOKEN_ORDER ? 'e-futures-order__token-order-buy' : 'e-futures-order__token-order-sell']"
      >
        {{ orderType === BUY_TOKEN_ORDER ? 'Buy' : 'Sell'}}
      </div>
    </div>
    <div class="e-futures-order__table-col">
      {{createdDateString}}
    </div>
    <div class="e-futures-order__table-col">
      <e-big-number :value="amount" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="3"/>

      <span class="e-futures-order__short-name">
        {{tokenShortName}}
      </span>
    </div>
    <div class="e-futures-order__table-col">
      <e-big-number :value="unitPrice" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="9"/>
      <e-icon :src="iconEarned" class="e-futures-order__icon-earned" />
    </div>
    <div class="e-futures-order__table-col">
      <e-big-number :value="amountFilled" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="3"/>
      <span class="e-futures-order__short-name">{{tokenShortName}}</span>
    </div>
    <div class="e-futures-order__table-col">
      <div
        v-if="allowedDelete"
        @click="handleCancel({ orderHash, orderType })"
        class="e-futures-order__delete-button"
        :class="[isTooSmall && isActive ? 'e-futures-order__delete-button_small-order' : '']"
      >
        cancel
      </div>
      <div
        v-else
        class="e-futures-order__condition"
      >
        <div class="e-futures-order__condition-hint">
          <template v-if="isCanceled">
            canceled order @ {{canceledDateString}}
          </template>
          <template v-else>
            finished order @ {{filledDateString}}
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import { BigNumber } from 'bignumber.js';
import { dateToString } from '@/util/dates';
import { ETH_DECIMALS } from '@/util/constants';

// SVG icons
import iconEarned from '@/icons/e-icon_earned.svg';
import iconFlag from '@/icons/e-icon_flag.svg';
import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';
import iconTrash from '@/icons/e-icon_trash.svg';

// Components
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import ETokenIcon from '@/components/ETokenIcon';
import EOrderAttentionHint from '@/components/EOrderAttentionHint';

export default {
  name: 'the-futures-order-row',

  props: {
    //   (  )
    orderType: {
      type: String,
    },

    orderHash: {
      type: String,
      required: true,
    },

    //    
    amount: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // ,     ( )
    amountFilled: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //  ,     ( )
    amountApproved: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // ,       ( )
    amountPanding: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //   
    createdAt: {
      type: Date,
      default: null,
    },

    //   
    canceledAt: {
      type: Date,
      default: null,
    },

    //      
    fulfilledAt: {
      type: Date,
      default: null,
    },

    isActive: {
      type: Boolean,
      default: false,
    },

    isStopped: {
      type: Boolean,
      default: false,
    },

    //    
    isCanceled: {
      type: Boolean,
      default: false,
    },

    //  
    unitPrice: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //  
    stopPrice: {
      type: BigNumber,
      default: () => null,
    },

    //       
    decimals: {
      type: Number,
      default: 18,
    },

    //    
    tokenShortName: {
      type: String,
      default: '',
    },


    // URL  
    tokenImage: {
      type: String,
      default: '',
    },

    //     
    allowedDelete: {
      type: Boolean,
      default: true,
    },
  },

  computed: {
    ...mapGetters({
      gasPrice: 'blockchain/backendGasPrice',
      takerGasLimit: 'blockchain/tradeTakerGasLimit',
      theme: 'ui/getTheme',
    }),

    //     (     )
    isFilled() {
      return !this.isCanceled && !this.isActive;
    },

    amountLeft() {
      let res = BigNumber(0);

      if (this.amount && this.amountFilled) {
        res = this.amount.minus(this.amountFilled);
      }

      return res;
    },

    barApproved() {
      return this.amountApproved.div(this.amount.div(100)).toFixed(0);
    },

    barPanding() {
      return this.amountPanding.div(this.amount.div(100)).toFixed(0);
    },

    createdDateString() {
      return this.createdAt ? dateToString(this.createdAt) : null;
    },

    canceledDateString() {
      return this.canceledAt ? dateToString(this.canceledAt, '') : null;
    },

    filledDateString() {
      return this.fulfilledAt ? dateToString(this.fulfilledAt, '') : null;
    },

    /**
     *     (   * )
     */
    ethAmount() {
      const { amount, unitPrice, decimals } = this;
      return amount.times(unitPrice).shiftedBy(ETH_DECIMALS - decimals);
    },

    //    ,   
    // true if amountLeft < 10 * gasFee
    isTooSmall() {
      const { decimals, amountLeft, unitPrice, gasPrice, takerGasLimit } = this;
      const avaliableAmountEth = amountLeft.times(unitPrice).shiftedBy(ETH_DECIMALS - decimals);
      const gasFeeAmountEth = gasPrice.times(takerGasLimit);
      return avaliableAmountEth.lt(gasFeeAmountEth.times(10));
    },
  },

  created() {
    Object.assign(this, {
      iconEarned,
      iconFlag,
      iconLoss,
      iconProfit,
      iconTrash,
    });
  },

  methods: {
    ...mapMutations({
      handleCancel: 'futuresOrders/CONFIRMATION_CANCEL_ORDER',
    }),
  },

  components: {
    EBigNumber,
    EIcon,
    EOrderAttentionHint,
    ETokenIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-futures-order {
  $this: &;

  height: 34px;
  font-size: 10px;
  color: #09168a;
  box-shadow: 0 1px 3px rgba(black, .08);
  background: #fff;
  border-radius: 3px;
  margin-bottom: 1px;
  display: flex;
  align-items: center;
  position: relative;
  transition: background-color .15s ease-in-out;

  &:hover {
    z-index: 1;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__table-col {
    display: flex;
    align-items: center;
    flex: 1 1 20%;
    padding: 0 4px;
    box-sizing: border-box;

    &:first-child {
      padding-left: 10px;
    }

    &:last-child {
      padding-right: 10px;
    }

    &:nth-child(1) {
      flex: 0 0 50px;
    }

    &:nth-child(2) {
      flex: 1 1 21%;
      min-width: 125px;
    }

    &:nth-child(3) {
      justify-content: flex-end;
    }

    &:nth-child(4) {
      justify-content: flex-end;
    }

    &:nth-child(5) {
      justify-content: flex-end;
    }

    &:nth-child(6) {
      flex: 0 0 59px;
      justify-content: flex-end;
    }

    .e-big-number {
      position: relative;
    }
  }

  &__token-order {
    width: 36px;
    height: 14px;
    box-sizing: border-box;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 9px;
    letter-spacing: 0;
    text-align: center;
  }

  &__token-order-buy {
    color: #1FCE43;
    background: rgba(31,206,67,0.10);
  }

  &__token-order-sell {
    color: #DC4727;
    background: rgba(220,71,39,0.10);
  }

  &__short-name {
    font-weight: 500;
    text-transform: uppercase;
    color: #09168a;
    text-align: right;
    padding-left: 2px;
  }

  &__icon-earned {
    width: 9px;
    height: 9px;
    margin-left: 1px;

    svg {
      fill: #09168a;
    }
  }

  &__delete-button {
    width: 45px;
    height: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #DC4727;
    color: #fff;
    font-size: 8px;
    text-transform: uppercase;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color .15s ease-in-out;

    &:hover {
      background: rgba(#DC4727, .8);
    }
  }

  .e-big-number {
    font-size: 10px;
    font-weight: 600;
    color: #09168a;

    &__leading-zeros,
    &__trailing-zeros {
      opacity: .5;
    }

    &__int {
      .e-big-number__main + .e-big-number__trailing-zeros {
        opacity: 1;
      }
    }
  }

  &_buy {
    #{$this}__amount-tokens {
      color: $color-green;
    }

    #{$this}__rail-fill {
      background-color: $color-green;
    }

    #{$this}__rail-fill_filled {
      opacity: 0.4;
    }

    #{$this}__hint {
      color: $color-green;
    }
  }

  &_sell {
    #{$this}__amount-tokens {
      color: $color-red;
    }

    #{$this}__rail-fill {
      background-color: $color-red;
    }

    #{$this}__rail-fill_filled {
      opacity: 0.4;
    }

    #{$this}__hint {
      color: $color-red;
    }
  }

  //  
  &_canceled {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      background-image: repeating-linear-gradient(60deg, #fff, #fff 6px, #fef8f8 0, #fef8f8 8px)
    }

    #{$this}__condition {
      background-color: rgba(#DA4830, .38);
      border: 2px solid #DA4830;
    }
  }

  &_filled {
    #{$this}__condition {
      background-color: #baf5c6;
      border: 2px solid #1FCE43;
    }
  }

  &__condition {
    border-radius: 50%;
    height: 8px;
    margin-right: 7px;
    margin-top: 0;
    width: 8px;
    position: relative;
    z-index: 2;

    &:before {
      content: '';
      position: absolute;
      top: -5px;
      right: -5px;
      bottom: -5px;
      left: -5px;
      cursor: pointer;
    }

    &:hover {
      #{$this}__condition-hint {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__condition-hint {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: $transition-time;
    top: calc(100% + 5px);
    right: 0;
    background-color: #fff;
    color: $color-dark-blue;
    font-size: 12px;
    font-weight: 400;
    line-height: 1;
    box-shadow: 0 3px 10px rgba(148, 165, 180, 0.14);
    border-radius: 3px;
    padding: 5px 10px 5px;
    white-space: nowrap;
  }

  &__dark {
    background: #1C222D;
    color: rgba(#8D9098, .5);

    #{$this}__icon-status svg path:nth-child(2) {
      fill: #232323
    }

    #{$this}__price {
      color: #5C6062;

      .e-big-number {
        color: #5C6062;

        &__int,
        &__leading-zeros {
          color: #fff;
        }
      }

      #{$this}_buy & {
        color: #5C6062;
      }

      #{$this}_sell & {
        color: #5C6062;
      }
    }

    #{$this}__price_total {
      color: #5C6062;

      .e-big-number {
        color: #5C6062;

        &__int,
        &__leading-zeros {
          color: #5C6062;
        }
      }

      #{$this}_buy & {
        color: #5C6062;
      }

      #{$this}_sell & {
        color: #5C6062;
      }
    }

    #{$this}__price-icon .e-icon svg {
      fill: #8d9098;
    }

    #{$this}__top {
      border-bottom-color: #3a3b3c;
    }

    &#{$this}_filled {
      #{$this}__condition {
        background-color: #205d2c;
      }
    }

    #{$this}__condition-hint {
      background: #1a1a1a;
      color: #5C6062;
      box-shadow: 0 3px 10px rgba(#000, 0.14);
    }

    #{$this}__filled,
    #{$this}__left {
      color: #fff;
    }

    #{$this}__timer,
    #{$this}__left #{$this}__hint {
      color: #8d9098;
    }

    #{$this}__delete .e-icon svg {
      fill: #8d9098;

      &:hover {
        fill: $color-blue-dark-mode;
      }
    }

    &#{$this}_canceled:before {
      background-image: repeating-linear-gradient(60deg, #1C222D, #1C222D 6px, #181d26 0, #181d26 8px);
    }

    #{$this}__rail {
      background: #505360;
    }

    #{$this}__short-name {
      color: #8D9098;
    }

    #{$this}__icon-earned {
      svg {
        fill: #8D9098;
      }
    }

    #{$this}__delete-button {
      background: rgba(92,96,98, .4);
      color: #8D9098;

      &:hover {
        background: rgba(92,96,98, .6);
      }
    }

    .e-big-number {
      color: #8D9098;
    }
  }
}

@keyframes blink {
  0%, 100% {
    opacity: 0.1;
  }
  50% {
    opacity: 0.8;
  }
}

</style>



