<template>
  <form
    class="the-order"
    :class="[ `the-order__${theme}`]"
    @submit.prevent="submitOrder"
  >
    <div class="the-order__inputs the-order__inputs-top">
      <div class="the-order__input-wrapper the-order__box full-width">
        <div class="the-order__switch-option">
          <div
            class="the-order__switch-option-buy"
            :class="{'the-order__switch-option-active': buyOrSell === BUY_TOKEN_ORDER}"
            @click="buyOrSell = BUY_TOKEN_ORDER"
          >
            Buy
          </div>
          <div
            class="the-order__switch-option-sell"
            :class="{'the-order__switch-option-active': buyOrSell === SELL_TOKEN_ORDER}"
            @click="buyOrSell = SELL_TOKEN_ORDER"
          >
            Sell
          </div>
        </div>
      </div>
    </div>

    <div v-if="positionToClose" class="the-order__position-close">
      Close Long position <b><e-big-number :value="positionToClose.size" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="4"/> BTC</b>
      <br>
      with <b><e-big-number :value="positionToClose.price" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="2"/> USD</b> entry price

      <button type="button" class="the-order__position-close-close" @click="closePositionInfo">
        <e-icon :src="iconClose"/>
      </button>
    </div>

    <div v-else class="the-order__inputs">
      <div class="the-order__input-wrapper">
        <div class="the-order__select" v-click-outside="hideDropdownMarginType">
          <div class="the-order__select-selection" @click="toggleDropdownMarginType">
            <div class="the-order__select-selection-text">
              <div class="the-order__select-label">
                Margin
              </div>
              <div class="the-order__select-value">
                {{ activeContract.margin }}x
              </div>
            </div>
            <e-icon :src="iconArrowDowner"/>
          </div>
          <div
            v-if="dropdownMarginTypeExpanded"
            class="the-order__select-dropdown"
            @click="toggleDropdownMarginType"
          >
            <div
              class="the-order__select-dropdown-item"
              v-for="(_marginContracts, marginName) in margins"
              :key="marginName"
              @click="handleSelectMargin(marginName)"
              v-if="activeContract.margin !== marginName"
            >
              {{marginName}}x
            </div>
          </div>
        </div>
      </div>
      <div class="the-order__input-wrapper">
        <div class="the-order__select" v-click-outside="hideDropdownExpirationDate">
          <div class="the-order__select-selection" @click="toggleDropdownExpirationDate">
            <div class="the-order__select-selection-text">
              <div class="the-order__select-label">
                Expiration date
              </div>
              <div class="the-order__select-value">
                {{formatExpires(activeContract.expiresInSeconds)}}
                <span class="the-order__select-value-hint" style="display: none">HINT</span>
              </div>
            </div>
            <e-icon :src="iconArrowDowner"/>
          </div>
          <div
            v-if="dropdownExpirationDateExpanded"
            class="the-order__select-dropdown"
            @click="toggleDropdownExpirationDate"
          >
            <div
              class="the-order__select-dropdown-item"
              v-for="contract of margins[activeContract.margin]"
              :key="contract.id"
              @click="changeActiveContractRoute(contract.futuresContractHash)"
            >
              {{formatExpires(contract.expiresInSeconds)}}
            </div>
          </div>
        </div>
      </div>
    </div>

    <component
      :is="({
        [ORDER_TYPE_LIMIT]: 'the-futures-order-form-limit',
        [ORDER_TYPE_MARKET]: 'the-futures-order-form-market',
        [ORDER_TYPE_STOP_LIMIT]: 'the-futures-order-form-stop-limit',
      })[orderType]"
      :key="orderType"
      v-bind="orderParams"
      :anyDirty.sync="anyDirty"
      :buy-or-sell="buyOrSell"
      class="the-order__type-inputs"
      @input="v => { orderParams = v }"
      @update:invalid="v => { invalid = v }"
    >
      <template slot="submit">
        <button
          v-if="buyOrSell === SELL_TOKEN_ORDER"
          key="sell"
          type="submit"
          class="the-order__submit-btn the-order__sell-btn"
          :disabled="invalid"
        >
          Sell {{ orderParams.amount | shortenNumber }} {{ activeContractName }}
        </button>
        <button
          v-else
          key="buy"
          type="submit"
          class="the-order__submit-btn the-order__buy-btn"
          :disabled="invalid"
        >
          Buy {{ orderParams.amount | shortenNumber }} {{ activeContractName }}
        </button>
      </template>

      <template slot="collateral">
        <div v-if="positionToClose" class="the-order__change-wrapper">
          <div class="the-order__profit-value">
            <span class="the-order__change the-order__profit" v-if="positionToClose.unrealizedPL.isGreaterThanOrEqualTo(0)">
              +<e-big-number :value="positionToClose.unrealizedPL" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="9"/> ETH
            </span>
            <span class="the-order__change the-order__loss" v-else>
              -<e-big-number :value="positionToClose.unrealizedPL.abs()" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="9"/> ETH
            </span>
          </div>
          <div class="the-order__change-label">
            <span v-if="positionToClose.unrealizedPL.isGreaterThanOrEqualTo(0)">
              Profit
            </span>
            <span v-else>
              Loss
            </span>
          </div>
        </div>
        <div v-else class="the-order__price-source">
          <div class="the-order__price-source-label">Price Source</div>
          <div class="the-order__price-source-value">Bitstamp</div>
        </div>
      </template>
    </component>

    <div class="the-order__prices">
      <div v-if="!positionToClose" class="the-order__prices-item">
        <div class="the-order__prices-item-value">
          <span v-if="collateral !== null">
            <span class="the-order__prices-item-num">
              <e-big-number :value="collateral" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="6"/>
            </span>
            ETH
          </span>
          <span v-else>--</span>
        </div>
        <div class="the-order__prices-item-label">
          <e-tooltip position="right">
            <template slot="label">
              Collateral
            </template>
            <template slot="message">
              The amount that will be reserved on your account to cover the maximum possible loss for this order. The collateral will be released once the position is closed or settled.
            </template>
          </e-tooltip>
        </div>
      </div>

      <div class="the-order__prices-item">
        <div class="the-order__prices-item-value">
          <span v-if="takeProfit !== null">
            <e-big-number :value="takeProfit" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="2"/> USD
          </span>
          <span v-else>--</span>
        </div>
        <div class="the-order__prices-item-label">
          <e-tooltip position="left">
            <template slot="label">
              Take Profit Price
            </template>
            <template slot="message">
              If the Take Profit price is reached, the position automatically closes and your profit will be settled. You can close the position earlier by creating an oposite order.
            </template>
          </e-tooltip>
        </div>
      </div>

      <div class="the-order__prices-item">
        <div class="the-order__prices-item-value">
          <span v-if="liquidation !== null">
            <e-big-number :value="liquidation" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="2"/> USD
          </span>
          <span v-else>--</span>
        </div>
        <div class="the-order__prices-item-label">
          <e-tooltip position="left">
            <template slot="label">
              Liquidation Price
            </template>
            <template slot="message">
              If the price reaches the liquidation price, you will lose all your collateral.
            </template>
          </e-tooltip>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import BigNumber from 'bignumber.js';
import get from 'lodash/get';
import { mapState, mapGetters } from 'vuex';
import balanceMixin from '@/util/mixins/balanceMixin';
import shortenNumber from '@/util/format/shortenNumber';

import TheFuturesOrderFormLimit from '@/containers/blocks/TheFuturesOrderFormLimit';
import TheFuturesOrderFormMarket from '@/containers/blocks/TheFuturesOrderFormMarket';
import TheFuturesOrderFormStopLimit from '@/containers/blocks/TheFuturesOrderFormStopLimit';
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';
import ETooltip from '@/components/ETooltip';

import iconArrowDowner from '@/icons/e-icon_arrow_downer.svg';
import iconClose from '@/icons/e-icon_close.svg';

import { CREATE_ORDER_PENDING, CREATE_ORDER_SUCCESS } from '@/store/constants';
import {
  ASYNC_STATES,
  BUY_TOKEN_ORDER,
  FUTURES_PRICE_DECIMALS,
  FUTURES_AMOUNT_DECIMALS,
} from '@/util/constants';
import futuresCalc from '@/util/futuresCalc';
import { getContracts } from '@/api/backend/futures/contracts';

export default {
  name: 'the-futures-order',

  data() {
    return {
      invalid: true,
      anyDirty: false,
      dropdownMarginTypeExpanded: false,
      dropdownExpirationDateExpanded: false,
      expirationDate: '1july',
      loadMarginsState: ASYNC_STATES.INITIAL,
      // Margins
      margins: {},
      refreshMarginsIntervalId: null,
    };
  },

  computed: {
    orderType: {
      get() {
        return this.$store.state.futuresActiveOrder.orderType;
      },

      set(orderType) {
        this.$store.commit('futuresActiveOrder/SET_CREATE_ORDER_TYPE', orderType);
        this.hideDropdownOrderType();
      },
    },

    buyOrSell: {
      get() {
        return this.$store.state.futuresActiveOrder.buyOrSell;
      },

      set(buyOrSell) {
        this.$store.commit('futuresActiveOrder/SET_CREATE_ORDER_BUY_OR_SELL', buyOrSell);
      },
    },

    orderParams: {
      get() {
        return this.$store.state.futuresActiveOrder.orderParams;
      },

      set(orderParams) {
        this.$store.commit('futuresActiveOrder/SET_CREATE_ORDER_NUMBERS', orderParams);
      },
    },

    collateral() {
      if (!this.activeContract || !this.orderParams.amount || !this.orderParams.limitPrice) {
        return null;
      }

      // Long
      if (this.buyOrSell === BUY_TOKEN_ORDER) {
        return futuresCalc.calculateCollateral(
          this.activeContract.floorPrice,
          BigNumber(this.orderParams.limitPrice).shiftedBy(FUTURES_PRICE_DECIMALS),
          BigNumber(this.orderParams.amount).shiftedBy(FUTURES_AMOUNT_DECIMALS),
          true,
          this.activeContract.inversed,
          this.activeContract.multiplier,
        );
      }
      // Short
      return futuresCalc.calculateCollateral(
        this.activeContract.capPrice,
        BigNumber(this.orderParams.limitPrice).shiftedBy(FUTURES_PRICE_DECIMALS),
        BigNumber(this.orderParams.amount).shiftedBy(FUTURES_AMOUNT_DECIMALS),
        false,
        this.activeContract.inversed,
        this.activeContract.multiplier,
      );
    },

    liquidation() {
      if (!this.activeContract) {
        return null;
      }

      return this.buyOrSell === BUY_TOKEN_ORDER
        ? this.activeContract.floorPrice
        : this.activeContract.capPrice;
    },

    takeProfit() {
      if (!this.activeContract) {
        return null;
      }

      return this.buyOrSell === BUY_TOKEN_ORDER
        ? this.activeContract.capPrice
        : this.activeContract.floorPrice;
    },

    readyToSubmit() {
      const loaded = !this.$store.getters['futuresActiveOrder/orderMinEthValue'].isNaN();
      return (
        loaded &&
        this.lastActiveOrderStatus !== CREATE_ORDER_PENDING &&
        !this.invalid
      );
    },

    ...mapState({
      lastActiveOrderStatus: state => state.futuresActiveOrder.status,
      activeContract: state => state.futuresActiveContract.activeContract,
      activeFuturesSmartContract: state => state.contracts.activeFuturesContract,
    }),

    ...mapGetters({
      activeContractName: 'futuresActiveContract/contractName',
      positionToClose: 'futuresPositions/positionToClose',
      theme: 'ui/getTheme',
    }),
  },

  watch: {
    activeContractName: 'resetForm',

    lastActiveOrderStatus(status) {
      if (status === CREATE_ORDER_SUCCESS) {
        this.resetForm();
      }
    },

    activeContract() {
      // Load margins when active futures contract is changed
      this.loadMargins();
      this.resetForm();
    },
  },

  created() {
    Object.assign(this, { iconArrowDowner, iconClose });
    this.$store.dispatch('updateAppConfig'); // for orderMinEthValue
    this.startListeningToEthBalance();
    this.startListeningToTokenBalance();

    this.loadMargins();

    this.fetchIntervalId = setInterval(this.loadMargins.bind(this), 60e3);
  },

  beforeDestroy() {
    clearInterval(this.fetchIntervalId);
    this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);

    this.stopListeningToTokenBalance();
    this.stopListeningToEthBalance();
  },

  methods: {
    toggleDropdownMarginType() {
      this.dropdownMarginTypeExpanded = !this.dropdownMarginTypeExpanded;
    },

    hideDropdownMarginType() {
      this.dropdownMarginTypeExpanded = false;
    },

    toggleDropdownExpirationDate() {
      this.dropdownExpirationDateExpanded = !this.dropdownExpirationDateExpanded;
    },

    hideDropdownExpirationDate() {
      this.dropdownExpirationDateExpanded = false;
    },

    submitOrder() {
      this.$store.dispatch('futuresActiveOrder/startOrderConfirmation', {
        buyOrSell: this.buyOrSell,
        orderType: this.orderType,
        orderParams: this.orderParams,
      });
    },

    handleSelectMargin(marginName) {
      const firstExpContract = this.margins[marginName][this.activeContract.expiresInSeconds];
      this.changeActiveContractRoute(firstExpContract.futuresContractHash);
    },

    async loadMargins() {
      let contracts = [];
      this.loadMarginsState = ASYNC_STATES.PENDING;
      try {
        contracts = await getContracts({
          // contractAddress: this.activeFuturesSmartContract.contractAddress,
          futuresAssetHash: this.activeContract.futuresAssetHash,
          contractState: [0, 10],
          isHidden: false,
        });
        this.loadMarginsState = ASYNC_STATES.SUCCESS;
      } catch (err) {
        this.loadMarginsState = ASYNC_STATES.ERROR;
      }

      const result = {};

      for (let i = 0; i < contracts.length; i += 1) {
        const contract = contracts[i];

        if (result[contract.margin] === undefined) {
          result[contract.margin] = {};
        }

        result[contract.margin][contract.expiresInSeconds] = contract;
      }

      const contractAfterRefresh = get(result, [this.activeContract.margin, this.activeContract.expiresInSeconds]);
      if (!contractAfterRefresh) {
        // If another contract with same margin and exp. date not found, then wait until new contract will be available
        return;
      }
      if (contractAfterRefresh.id !== this.activeContract.id) {
        this.changeActiveContractRoute(contractAfterRefresh.futuresContractHash);
        return;
      }

      // Set margins
      this.margins = result;
    },

    changeActiveContractRoute(contractHash) {
      this.$router.push({ name: 'future', params: { contract: contractHash } });
    },

    formatExpires(seconds) {
      const days = Math.round(seconds / 86400);
      if (days >= 1) {
        return `${days} day${days > 1 ? 's' : ''}`;
      }

      const hours = Math.round(seconds / 3600);

      return `${hours} hour${hours > 1 ? 's' : ''}`;
    },

    resetForm() {
      this.orderParams = {
        amount: '',
      };
      this.anyDirty = false;
    },

    closePositionInfo() {
      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
    },
  },

  components: {
    TheFuturesOrderFormLimit,
    TheFuturesOrderFormMarket,
    TheFuturesOrderFormStopLimit,
    EIcon,
    EBigNumber,
    ETooltip,
  },

  filters: { shortenNumber },

  mixins: [balanceMixin],
};
</script>

