    margin-top: -6px;
  }

  &__price-source {
    background: #fff;
    border: 1px solid #d9dade;
    padding: 8px 18px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
  }

  &__price-source-label {
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    color: #9A9EA7;
  }

  &__price-source-value {
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    color: #5C6062;
  }

  &__prices-item {
    flex: 1 1 33.3%;
    height: 46px;
    padding: 8px 0;
    box-sizing: border-box;
  }

  &__prices-item-value {
    width: 100%;
    color: #5C6062;
    font-size: 11px;
    font-weight: 600;
    text-align: center;
    user-select: none;
  }

  &__prices-item-label {
    width: 100%;
    font-size: 9px;
    text-transform: uppercase;
    font-weight: 600;
    color: #5C6062;
    text-align: center;
    user-select: none;

    .e-tooltip__content {
      width: 180px;
    }

    @include r(1500) {
      font-size: 8px;
    }
  }

  &__dark {
    border-bottom-color: #212121;

    #{$this}__switch-option {
      border-color: transparent;

      #{$this}__switch-option-buy,
      #{$this}__switch-option-sell {
        background: $dark-background-lighter;
        color: rgba(#ffffff, .8);

        &:hover {
          background: rgba(#12151b, .8);
          color: #fff;
        }
      }

      #{$this}__switch-option-active {
        color: #fff;
        background: rgba(#ffffff, .08);

        &:hover {
          background: rgba(#ffffff, .08);
        }
      }

      #{$this}__switch-option-sell {
        border-left-color: transparent;
      }
    }

    #{$this}__select {
      background: $dark-background-lighter;
      border-color: $dark-background-lighter;
    }

    #{$this}__prices-item-value {
      color: #fff;
    }

    #{$this}__select-label {
      color: #5c6062;
    }

    #{$this}__select-value {
      color: #0097FE;
    }

    #{$this}__select-value-hint {
      color: #fff;
    }

    #{$this}__select .e-icon svg {
      fill: #fff;
    }

    #{$this}__select-dropdown {
      border-color: #1a1a1a;
      border-top-color: $dark-background-lighter;
    }

    #{$this}__select-dropdown-item {
      background: $dark-background-lighter;
      color: #fff;
      border-color: rgba(#ffffff, .1);

      &:hover {
        background: rgba($dark-background,.92);
      }
    }

    .e-input-number__input {
      color: #fff;
    }

    .e-input-number.focused {
      .e-input-number__box {
        background-color: $dark-background-lighter;

        &::before {
          border: none;
        }
      }

      .e-input-number__unit {
        color: #fff;
      }
    }

    .e-input-number__box {
      border-color: transparent !important;
      background-color: $dark-background-lighter !important;
    }

    .e-input-number__unit {
      color: #5c6062;
    }

    .e-input-number:hover:not(.e-input-number_non-editable):not(.focused) .e-input-number__unit {
      color: #fff;
    }

    #{$this}__submit-btn {
      &:disabled {
        background: #8D9098;
        color: #5C6062;
      }
    }

    #{$this}__price-source {
      background: #121722;
      border-color: #282C38;
    }

    #{$this}__price-source-label {
      color: #5C6062;
    }

    #{$this}__price-source-value {
      color: #FFFFFF;
    }
  }
}
</style>



