<template>
  <div class="the-order-form-stop-limit">
    <e-input-number
      :value="limitPrice"
      :decimals="FUTURES_PRICE_DECIMALS"
      :invalid="$v.limitPrice.$error"
      label="Limit Price"
      measurement-unit="ETH"
      class="the-order-form-stop-limit__input the-order-form-stop-limit__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onLimitPriceInput"
    />

    <e-input-number
      :value="amount"
      :decimals="FUTURES_AMOUNT_DECIMALS"
      :invalid="$v.amount.$dirty && ($v.amount.$invalid || !enoughBalance)"
      label="Amount"
      :measurement-unit="activeContractName"
      class="the-order-form-stop-limit__input the-order-form-stop-limit__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onAmountInput"
    />

    <e-input-number
      :value="stopPrice"
      :decimals="ETH_DECIMALS"
      :invalid="$v.stopPrice.$error"
      label="Stop Price"
      measurement-unit="ETH"
      class="the-order-form-stop-limit__input the-order-form-stop-limit__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onStopPriceInput"
    />

    <e-input-number
      :value="total"
      disabled
      :decimals="ETH_DECIMALS"
      label="Total"
      measurement-unit="USD"
      class="the-order-form-stop-limit__input the-order-form-stop-limit__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
    />

    <div class="the-order-form-stop-limit__submit the-order-form-stop-limit__item">
      <slot name="submit"/>
    </div>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';
import { validationMixin } from 'vuelidate';
import { required, decimal } from 'vuelidate/lib/validators';

import { ETH_DECIMALS, BUY_TOKEN_ORDER, SELL_TOKEN_ORDER } from '@/util/constants';
import { maxDecimalPlaces, greaterThanZero } from '@/util/validators';

import EInputNumber from '@/components/EInputNumber';

export default {
  name: 'the-futures-order-form-stop-limit',
  inheritAttrs: false,

  props: {
    stopPrice: {
      type: String,
      default: '',
    },

    limitPrice: {
      type: String,
      default: '',
    },

    amount: {
      type: String,
      default: '',
    },

    buyOrSell: {
      type: String,
      required: true,
      validator: v => [BUY_TOKEN_ORDER, SELL_TOKEN_ORDER].includes(v),
    },

    anyDirty: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    total() {
      const limitPrice = BigNumber(this.limitPrice);
      const amount = BigNumber(this.amount);
      return limitPrice.isNaN() || amount.isNaN()
        ? ''
        : amount.times(limitPrice).toFixed();
    },

    invalid() {
      return this.$v.$invalid;
    },

    ...mapGetters({
      activeContractName: 'futuresActiveContract/contractName',
    }),
  },

  validations: {
    stopPrice: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces: maxDecimalPlaces(ETH_DECIMALS),
    },

    limitPrice: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces: maxDecimalPlaces(ETH_DECIMALS),
    },

    amount: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces(value) {
        return maxDecimalPlaces(ETH_DECIMALS)(value);
      },
    },
  },

  watch: {
    invalid: {
      immediate: true,
      handler(invalid) {
        this.$emit('update:invalid', invalid);
      },
    },

    '$v.$anyDirty': function handler(anyDirty) {
      this.$emit('update:anyDirty', anyDirty);
    },

    anyDirty: {
      immediate: true,
      handler(anyDirty) {
        if (anyDirty) {
          this.$v.$reset();
        }
      },
    },
  },

  created() {
    this.onInput();
  },

  methods: {
    onInput({ stopPrice = this.stopPrice, limitPrice = this.limitPrice, amount = this.amount } = {}) {
      this.$emit('input', { stopPrice, limitPrice, amount });
    },

    onStopPriceInput(stopPrice) {
      this.$v.stopPrice.$touch();
      this.onInput({ stopPrice });
    },

    onLimitPriceInput(limitPrice) {
      this.$v.limitPrice.$touch();
      this.onInput({ limitPrice });
    },

    onAmountInput(amount) {
      this.$v.amount.$touch();
      this.onInput({ amount });
    },
  },

  components: {
    EInputNumber,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-order-form-stop-limit {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;

  &__item {
    height: 50px;
    margin-bottom: 12px;

    @include r(1200) {
      height: 40px;
      margin-bottom: 10px;
    }

    @include h(750) {
      height: 40px;
      margin-bottom: 10px;
    }

    @include rwh(2400, 850) {
      height: 60px;
      margin-bottom: 14px;
    }
  }

  &__input {
    width: calc(50% - 5px);
  }

  &__submit {
    width: 100%;
  }
}
</style>



