<template>
  <transition-modal>
    <div
      v-if="order"
      class="the-order-confirm"
    >
      <e-receipt
        title="Confirm order"
        @confirm="submit"
        @cancel="close"
      >
        <template slot="header">
          <e-receipt-item
            :buy="order.buyOrSell === BUY_TOKEN_ORDER"
            :sell="order.buyOrSell === SELL_TOKEN_ORDER"
            :token-name="displayedOrderType.replace('USD', '')"
            icon
          >
            <template slot="title">
              Order Type
            </template>
          </e-receipt-item>

          <e-receipt-item
            v-if="order.type === ORDER_TYPE_LIMIT"
            tokenName="USD"
          >
            <template slot="title">
              Price
            </template>
            <e-big-number
              slot="value"
              :value="order.limitPrice"
              :decimals="FUTURES_PRICE_DECIMALS"
              :decimal-places="2"
            />
          </e-receipt-item>

          <e-receipt-item
            v-if="order.type === ORDER_TYPE_STOP_LIMIT"
            :tokenName="contractName"
          >
            <template slot="title">
              Limit Price
            </template>
            <e-big-number
              slot="value"
              :value="order.limitPrice"
              :decimals="FUTURES_PRICE_DECIMALS"
              :decimal-places="6"
            />
          </e-receipt-item>

          <e-receipt-item v-else-if="order.type === ORDER_TYPE_MARKET">
            <template slot="title">
              Price for 1 {{ contractName }}
            </template>
            <div
              slot="value"
              class="the-order-confirm__value"
            >
              Market Price
            </div>
          </e-receipt-item>

          <e-receipt-item
            v-if="order.type === ORDER_TYPE_STOP_LIMIT"
            tokenName="ETH"
          >
            <template slot="title">
              Stop Price for 1 {{ contractName }}
            </template>
            <e-big-number
              slot="value"
              :value="order.stopPrice"
              :decimals="FUTURES_PRICE_DECIMALS"
              :decimal-places="6"
            />
          </e-receipt-item>

          <e-receipt-item tokenName="USD">
            <template slot="title">
              Amount
            </template>
            <template slot="value">
              <e-big-number :value="orderTotal" :decimals="FUTURES_AMOUNT_DECIMALS + FUTURES_AMOUNT_DECIMALS" :decimal-places="2"/>
            </template>
          </e-receipt-item>

          <e-receipt-item
            v-if="[ORDER_TYPE_LIMIT, ORDER_TYPE_STOP_LIMIT].includes(order.type)"
            :tokenName="displayedOrderType.replace('USD', '')"
          >
            <template slot="title">
              Transaction value
            </template>
            <template slot="value">
              <e-big-number :value="order.amount" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="6"/>
            </template>
          </e-receipt-item>

          <e-receipt-item
            v-else-if="order.type === ORDER_TYPE_MARKET"
          >
            <template slot="title">
              Transaction value
            </template>
            <div
              slot="value"
              class="the-order-confirm__value"
            >
              
            </div>
          </e-receipt-item>


          <e-receipt-item tokenName="ETH">
            <template slot="title">
              Collateral
            </template>
            <template slot="value">
              <e-big-number :value="collateral" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="6"/>
            </template>
          </e-receipt-item>

          <e-receipt-item>
            <template slot="title">
              Margin
            </template>
            <template slot="value">
              <e-big-number v-if="order.buyOrSell === BUY_TOKEN_ORDER" :value="activeContract.leverage.buy" :decimal-places="1" />
              <e-big-number v-else :value="activeContract.leverage.sell" :decimal-places="1" />
              X
            </template>
          </e-receipt-item>
        </template>
      </e-receipt>
    </div>
  </transition-modal>
</template>

<script>
import { mapGetters, mapState, mapMutations } from 'vuex';

import {
  BUY_TOKEN_ORDER,
  ORDER_TYPE_MARKET,
  ORDER_TYPE_STOP_LIMIT,
  SELL_TOKEN_ORDER,
} from '@/util/constants';

import futuresCalc from '@/util/futuresCalc';

import iconLink from '@/icons/e-icon_link.svg';

import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EBigNumber from '@/components/EBigNumber';
import EReceipt from '@/components/EReceipt';
import EReceiptItem from '@/components/EReceiptItem';

import TransitionModal from '@/containers/transitions/TransitionModal';

export default {
  name: 'the-futures-order-popup',

  computed: {
    ...mapGetters({
      contractBaseToken: 'futuresActiveContract/contractBaseToken',
      contractName: 'futuresActiveContract/contractName',
      order: 'futuresActiveOrder/orderConfirmation',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
    }),

    orderTotal() { return this.order ? this.order.limitPrice.times(this.order.amount) : 0; },

    displayedOrderType() {
      const prefix = {
        [ORDER_TYPE_MARKET]: 'Market',
        [ORDER_TYPE_STOP_LIMIT]: 'Stop',
      }[this.order.type] || '';
      const buyOrSell = {
        [BUY_TOKEN_ORDER]: 'Buy',
        [SELL_TOKEN_ORDER]: 'Sell',
      }[this.order.buyOrSell];
      return `${prefix} ${buyOrSell} ${this.contractName}`;
    },

    collateral() {
      if (!this.activeContract || !this.order.amount || !this.order.limitPrice) {
        return null;
      }

      // Long
      if (this.order.buyOrSell === BUY_TOKEN_ORDER) {
        return futuresCalc.calculateCollateral(
          this.activeContract.floorPrice,
          this.order.limitPrice,
          this.order.amount,
          true,
          this.activeContract.inversed,
          this.activeContract.multiplier,
        );
      }
      // Short
      return futuresCalc.calculateCollateral(
        this.activeContract.capPrice,
        this.order.limitPrice,
        this.order.amount,
        false,
        this.activeContract.inversed,
        this.activeContract.multiplier,
      );
    },
  },

  created() {
    Object.assign(this, { iconLink });
  },

  methods: {
    ...mapMutations({ close: 'futuresActiveOrder/ORDER_REJECT_CONFIRMATION' }),

    submit() {
      this.$store.dispatch('futuresActiveOrder/submitOrder', this.$store.state.futuresActiveOrder.orderConfirmation);
    },
  },

  components: {
    TransitionModal,
    EBigNumber,
    EButton,
    EIcon,
    EModal,
    EReceipt,
    EReceiptItem,
  },
};
</script>

<style lang="scss">
.the-order-confirm__value {
  color: #0D105B;
  font-weight: 600;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}
</style>



