<template>
  <div class="the-futures-and-my-orders" :class="[ `the-futures-and-my-orders__${theme}`]">
    <div
      class="the-futures-and-my-orders__tabs"
    >
      <e-tabs
        :tabs="tabs"
        v-model="activeTab"
      />
    </div>

    <the-news v-if="activeTab === NEWS_AND_ORDERS_TAB_NEWS && !isLogin"/>
    <the-futures-positions v-if="activeTab === NEWS_AND_ORDERS_TAB_POSITIONS"/>
    <the-futures-my-orders v-else-if="[NEWS_AND_ORDERS_TAB_ACTIVE, NEWS_AND_ORDERS_TAB_HISTORY].includes(activeTab)"/>
  </div>
</template>

<script>
/**
 *       
 *
 * @version 1.0.5
 */

import { mapGetters } from 'vuex';

import {
  NEWS_AND_ORDERS_TAB_ACTIVE,
  NEWS_AND_ORDERS_TAB_HISTORY,
  MY_ORDERS_MODE_ACTIVE,
  // NEWS_AND_ORDERS_TAB_CLOSED_POSITION,
  MY_ORDERS_MODE_HISTORY,
} from '@/store/constants';

// omponents
import TheNews from '@/containers/blocks/TheNews';
import TheFuturesPositions from '@/containers/blocks/TheFuturesPositions';
import TheFuturesMyOrders from '@/containers/blocks/TheFuturesMyOrders';
import ETabs from '@/components/ETabs';

const TAB_NEWS = {
  id: 4,
  title: 'News',
};

const TAB_POSITIONS = {
  id: 0,
  title: 'Positions',
};

const TAB_ACTIVE_ORDERS = {
  id: 1,
  title: 'Active orders',
};

// const TAB_CLOSED_POSITIONS = {
//   id: 2,
//   title: 'Closed positions',
// };

const TAB_HISTORY = {
  id: 3,
  title: 'Order history',
};

const TABS_FOR_USER_WITH_WALLET = [
  TAB_POSITIONS,
  TAB_ACTIVE_ORDERS,
  // TAB_CLOSED_POSITIONS,
  TAB_HISTORY,
];

const TABS_FOR_USER_WITHOUT_WALLET = [TAB_POSITIONS, TAB_NEWS];

export default {
  name: 'the-futures-positions-and-my-orders',

  computed: {
    /**
     *     
     */
    activeTab: {
      get() {
        return this.$store.state.ui.newsAndMyOrdersTab;
      },

      set(activeTab) {
        this.$store.commit('ui/SET_NEWS_AND_ORDERS_TAB', activeTab);

        // TODO: infer my orders mode from newsAndOrders mode
        if (activeTab === NEWS_AND_ORDERS_TAB_ACTIVE) {
          this.$store.commit('futuresOrders/SET_MY_ORDERS_MODE', MY_ORDERS_MODE_ACTIVE);
        } else if (activeTab === NEWS_AND_ORDERS_TAB_HISTORY) {
          this.$store.commit('futuresOrders/SET_MY_ORDERS_MODE', MY_ORDERS_MODE_HISTORY);
        }
        // else if (activeTab === NEWS_AND_ORDERS_TAB_CLOSED_POSITION) {
        //   console.log('NEWS_AND_ORDERS_TAB_CLOSED_POSITION opened');
        // }
      },
    },

    /**
     *   
     *    ,        ,    
     */
    tabs() {
      return this.isLogin ? TABS_FOR_USER_WITH_WALLET : TABS_FOR_USER_WITHOUT_WALLET;
    },

    ...mapGetters({
      isLogin: 'wallets/isLogin',
      theme: 'ui/getTheme',
    }),
  },

  components: {
    TheNews,
    TheFuturesPositions,
    TheFuturesMyOrders,
    ETabs,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-futures-and-my-orders {
  $this: &;

  position: relative;
  height: 100%;
  background: #fff;
  border: 1px solid #DBDCDD;
  border-radius: 3px;
  overflow: hidden;

  &__tabs {
    margin-bottom: 0;
    padding: 8px 10px;
    background: #fff;
    border-bottom: 1px solid #DBDCDD;
  }

  .e-container-scroll {
    overflow-y: scroll;
    height: calc(100% - 34px);

    &::-webkit-scrollbar-track {
      background-color: #fff;
    }
  }

  &__dark {
    background: #121722;
    border-color: #1e202b;

    #{$this}__tabs {
      background: #121722;
      border-color: #1e202b;
    }

    .e-container-scroll {
      &::-webkit-scrollbar-track {
        background-color: #121722;
      }
    }
  }
}
</style>



