<template>
  <div class="the-positions" :class="[ `the-positions__${theme}`]">
    <e-preloader v-if="isLoading"/>

    <div class="the-positions__table">
      <div class="the-positions__table-head">
        <div class="the-positions__table-col">Symbol</div>
        <div class="the-positions__table-col">Base</div>
        <div class="the-positions__table-col">Size</div>
        <div class="the-positions__table-col">Entry Price</div>
        <div class="the-positions__table-col">Mark Price</div>
        <div class="the-positions__table-col">Liq. Price</div>
        <div class="the-positions__table-col">Profit/Loss</div>
        <div class="the-positions__table-col">Collateral</div>
        <div class="the-positions__table-col">Expires</div>
        <div class="the-positions__table-col">Action</div>
      </div>

      <e-container-scroll
        ref="scroll"
        class="the-positions__wrapper"
      >
        <div class="the-positions__table-body">
          <div
            class="the-positions__table-body-item"
            v-for="pos in positions"
            :key="pos.positionHash"
          >
            <div class="the-positions__table-col">
              <div class="the-positions__symbol">{{ activeContractName }}</div>
            </div>

            <div class="the-positions__table-col">
              <div class="the-positions__base">{{ contractBaseToken.shortName }}</div>
            </div>

            <div class="the-positions__table-col">
              <span
                class="the-positions__size"
                :class="[pos.side ? 'the-positions__size-settle' : 'the-positions__size-close']"
              >
                <e-big-number :value="pos.size" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="4"/>
              </span>
            </div>

            <div class="the-positions__table-col">
              <span class="the-positions__price">
                <e-big-number :value="pos.price" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="2"/>
              </span>
            </div>

            <div class="the-positions__table-col">
                <span class="the-positions__price">
                  <e-big-number :value="pos.markPrice" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="2"/>
                </span>
            </div>

            <div class="the-positions__table-col">
                <span class="the-positions__price">
                  <e-big-number :value="pos.side ? activeContract.floorPrice : activeContract.capPrice" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="2"/>
                </span>
            </div>

            <div class="the-positions__table-col">
              <span class="the-positions__change the-positions__profit" v-if="pos.unrealizedPL.isGreaterThanOrEqualTo(0)">
                +<e-big-number :value="pos.unrealizedPL" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="9"/>
              </span>
              <span class="the-positions__change the-positions__loss" v-else>
                -<e-big-number :value="pos.unrealizedPL.abs()" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="9"/>
              </span>
            </div>

            <div class="the-positions__table-col">
              <span class="the-positions__collateral">
                 <e-big-number :value="pos.collateral" :decimals="FUTURES_AMOUNT_DECIMALS" :decimal-places="9"/>
                 ETH
               </span>
            </div>

            <div class="the-positions__table-col">
              <div class="the-positions__date-expires" v-if="pos.expiresInSeconds">
                {{pos.expiresInSeconds}}
              </div>
              <div v-else class="the-positions__date-expires-expired">
                Expired
              </div>
            </div>

            <div class="the-positions__table-col">
              <div v-if="!pos.expiresInSeconds" class="the-positions__label-settle">Settling</div>
              <div v-else class="the-positions__label the-positions__label-close" @click="settleClickHandler(pos.futuresContractHash, pos)">Close</div>
            </div>
          </div>
        </div>
      </e-container-scroll>
    </div>

    <!--<div-->
      <!--class="the-positions__error"-->
      <!--v-if="positions.length === 0"-->
    <!--&gt;-->
      <!--<e-panel-error :icon="errorIcon">-->
        <!--<template slot="title">-->
          <!--Positions-->
        <!--</template>-->
        <!--<template slot="descr">-->
          <!--<template>-->
            <!--No positions-->
          <!--</template>-->
        <!--</template>-->
      <!--</e-panel-error>-->
    <!--</div>-->
  </div>
</template>

<script>
// import { BigNumber } from 'bignumber.js';
import { mapGetters, mapState } from 'vuex';
import { ASYNC_STATES } from '@/util/constants';

import { dateToString } from '@/util/dates';
import { onReconnect, removeReconnectListener } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';

// Components
import EBigNumber from '@/components/EBigNumber';
import EContainerScroll from '@/components/EContainerScroll';
import EPanelError from '@/components/EPanelError';
import EPreloader from '@/components/EPreloader';
import EIcon from '@/components/EIcon/EIcon';

import errorIcon from '@/icons/e-icon_empty-trades.svg';
import iconClose from '@/icons/e-icon_close.svg';

export default {
  name: 'the-futures-positions',

  data() {
    return {
      errorIcon,
      iconClose,
      reconnectHandler: null,
      fetchIntervalId: null,
    };
  },

  computed: {
    ...mapGetters({
      activeContractName: 'futuresActiveContract/contractName',
      contractBaseToken: 'futuresActiveContract/contractBaseToken',
      isConnected: 'isConnected',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
      fetchPositionsState: state => state.futuresPositions.fetchPositionsState,
      positions: state => state.futuresPositions.positions,
    }),

    isLoading() {
      return this.fetchPositionsState === ASYNC_STATES.PENDING;
    },
  },

  watch: {
    /**
     *    
     */
    activeContract(newVal, oldVAl) {
      if (newVal !== oldVAl) {
        this.$store.dispatch('futuresPositions/fetchPositions');
      }
    },
  },

  mounted() {
    this.$store.dispatch('futuresPositions/fetchPositions');
    this.startReconnectListener();

    this.fetchIntervalId = setInterval(() => this.$store.dispatch('futuresPositions/fetchPositions'), 5e3);
  },

  beforeDestroy() {
    this.stopReconnectListener();

    clearInterval(this.fetchIntervalId);
  },

  methods: {
    dateToString,

    settleClickHandler(futuresContractHash, position) {
      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', position);
      this.$router.push({ name: 'future', params: { contract: futuresContractHash } });
    },

    startReconnectListener() {
      this.reconnectHandler = () => {
        this.$store.dispatch('futuresPositions/fetchPositions');
      };
      onReconnect(this.reconnectHandler);
      onResume(this.reconnectHandler);
    },

    stopReconnectListener() {
      if (!this.reconnectHandler) {
        return;
      }
      removeReconnectListener(this.reconnectHandler);
      removeResumeListener(this.reconnectHandler);
    },
  },

  components: {
    EIcon,
    EBigNumber,
    EContainerScroll,
    EPanelError,
    EPreloader,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-positions {
  $this: &;

  position: relative;
  height: calc(100% - 27px);
  overflow-x: scroll;

  &__wrapper {
    padding-left: 0;
  }

  &__error {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
  }

  &__table {
    width: 100%;
    height: 100%;
    color: #5C6062;
  }

  &__table-head,
  &__table-body-item {
    display: flex;
    align-items: center;
  }

  &__table-head {
    font-size: 8px;
    padding-right: 2px;
    text-transform: uppercase;
    letter-spacing: .5px;
    background: #1C222D;
    border-bottom: 1px solid #282C38;

    #{$this}__table-col {
      border-right: 1px solid #282C38;
    }
  }

  &__table-body-item {
    height: 34px;
    background: #121722;
    border-bottom: 1px solid #282C38;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__table-col {
    display: flex;
    align-items: center;
    flex: 1 1 16%;
    padding: 8px;
    box-sizing: border-box;
    border-right: 1px solid #1e202b;

    &:nth-child(1) {
      flex: 0 0 69px;
    }

    &:nth-child(2) {
      flex: 0 0 48px;
    }

    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6),
    &:nth-child(7),
    &:nth-child(8) {
      justify-content: flex-end;
      text-align: right;
    }

    &:nth-child(7) {
      flex: 0 0 13%;
    }

    &:nth-child(8) {
      flex: 0 0 16%;
    }

    &:nth-child(9) {
      display: flex;
      justify-content: flex-end;
      flex: 0 0 72px;
      padding: 8px 5px;
    }

    &:nth-child(10) {
      display: flex;
      justify-content: flex-end;
      flex: 0 0 46px;
      padding: 8px 5px;
    }

    &:last-child {
      border: 0;
    }

    .e-big-number {
      position: relative;
    }

    @include r(1439) {
      padding: 8px 5px;
    }
  }

  &__table-body {
    #{$this}__table-col {
      height: 34px;

      &:nth-child(7) {
        font-size: 10px;
        font-weight: 500;
      }
    }
  }

  &__symbol {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 42px;
    height: 17px;
    padding: 0 2px;
    color: #8D9098;
    font-size: 9px;
    letter-spacing: -.25px;
    text-transform: uppercase;
    font-weight: 600;
    border-radius: 4px;
    border: 1px solid rgba(92,96,98,0.40);
  }

  &__size,
  &__change,
  &__price,
  &__collateral {
    font-size: 11px;
    font-weight: 600;

    .e-big-number {
      font-size: 11px;
      font-weight: 600;

      &__leading-zeros,
      &__trailing-zeros {
        opacity: .5;
      }

      &__int {
        .e-big-number__main + .e-big-number__trailing-zeros {
          opacity: 1;
        }
      }

      @include r(1439) {
        font-size: 10px;
        font-weight: 500;
      }
    }
  }

  &__change,
  &__collateral {
    font-size: 10px;
    font-weight: 500;

    .e-big-number {
      font-size: 10px;
      font-weight: 500;
    }
  }

  &__price,
  &__collateral {
    color: #8D9098;

    .e-big-number {
      color: #8D9098;
    }
  }

  &__size-settle,
  &__profit {
    color: #1FCE43;
  }

  &__size-close,
  &__loss {
    color: #DA4830;
  }

  &__date {
    font-size: 9px;
    font-weight: 600;
    color: #8D9098;
    opacity: .5;
  }

  &__date-expires {
    color: #8D9098;
    font-size: 9px;
    font-weight: 500;
  }

  &__date-expires-expired {
    width: 100%;
    text-align: center;
    display: inline-block;
    font-size: 8px;
    font-weight: 500;
    text-transform: uppercase;
    padding: 1px 4px;
    border-radius: 3px;
    background: rgba(220,71,39,.2);
    color: #DC4727;
  }

  &__label {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 14px;
    text-align: center;
    border-radius: 2px;
    font-size: 8px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    transition: opacity .15s ease-in-out;

    &:hover {
      opacity: .85;
    }
  }

  &__label-close {
    background: #DA4830;
  }

  &__label-settle {
    font-size: 10px;
    color: #8D9098;
  }

  &__base {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 27px;
    height: 14px;
    border-radius: 2px;
    background: rgba(92,96,98,0.40);
    color: #8D9098;
    font-size: 8px;
    text-transform: uppercase;
    font-weight: 600;
  }

  &__close {
    width: 20px;
    height: 35px;
    margin-right: -8px;
    margin-left: 1px;
    cursor: pointer;
    opacity: .7;
    transition: opacity .15s ease-in-out;

    svg {
      width: 8px;
      height: 8px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      fill: rgb(218, 72, 48);
    }

    &:hover {
      opacity: 1;
    }
  }

  &__light {
    #{$this}__table {
      color: #5C6062;
    }

    #{$this}__table-head {
      background: #FBFBFD;
      border-color: #F0F6F9;

      #{$this}__table-col {
        border-color: #F0F6F9;
      }
    }

    #{$this}__table-body-item {
      background: #fff;
      border-color: #F0F6F9;
    }

    #{$this}__table-col {
      border-color: #F0F6F9;
    }

    #{$this}__symbol {
      color: #0D105B;
      border-color: rgba(92,96,98,.3);
    }

    #{$this}__base {
      background: rgba(92,96,98,.1);
      color: #8D9098;
    }

    #{$this}__price,
    #{$this}__collateral {
      color: #09168a;
    }

    #{$this}__price .e-big-number,
    #{$this}__collateral .e-big-number {
      color: #09168a;
    }

    #{$this}__label {
      color: #fff;
    }

    #{$this}__date {
      color: #8384AB;
    }

    #{$this}__date-expires-expired {
      background: rgba(220,71,39,.1);
    }
  }
}
</style>



